import React, { Component } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
} from "@coreui/react";

import API from "src/api";
import { history } from "src/history";

class AddTopics extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      preFix: "",
      badge: "",
      chapterId: this.props.match.params.id,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.submitButton = this.submitButton.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  }

  async submitButton() {
    await API.post("/topics/add", this.state)
      .then((response) => {
        console.log(response.code);
      })
      .catch((error) => {
        alert("Try Again");
      });
    this.resetButton();
  }

  resetButton = () => {
    this.setState((prevState) => ({
      title: "",
      preFix: "",
      badge: "",
    }));
  };

  render() {
    let { preFix, title, badge } = this.state;
    return (
      <>
        <CCol xs="12">
          <CCard>
            <CCardHeader>
              <span className="h4">Add Topics</span>
              {/* <Link to={{pathname:`/topics/${chapterId}`}} className="btn btn-outline-info float-right mx-2">
                Back
              </Link> */}
              <CButton
                className="btn btn-outline-info float-right mx-2"
                onClick={() => {
                  history.goBack();
                }}
              >
                Back
              </CButton>
            </CCardHeader>
            <CCardBody>
              <CForm
                action=""
                method="post"
                encType="multipart/form-data"
                className="form-horizontal"
              >
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="preFix">Title PreFix</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput
                      onChange={this.handleInputChange}
                      type="text"
                      id="preFix"
                      name="preFix"
                      value={preFix}
                      placeholder="Enter Chapter Prefix"
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="title">Title</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput
                      onChange={this.handleInputChange}
                      id="title"
                      name="title"
                      value={title}
                      placeholder="Title"
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="badge">Badge</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput
                      onChange={this.handleInputChange}
                      type="text"
                      id="badge"
                      name="badge"
                      value={badge}
                      placeholder="Enter Badge"
                    />
                  </CCol>
                </CFormGroup>
              </CForm>
            </CCardBody>
            <CCardFooter>
              <CButton
                type="submit"
                className="m-1"
                size="sm"
                color="primary"
                onClick={this.submitButton}
              >
                Submit
              </CButton>
              <CButton
                type="reset"
                size="sm"
                className="m-1"
                color="danger"
                onClick={this.resetButton}
              >
                Reset
              </CButton>
            </CCardFooter>
          </CCard>
        </CCol>
      </>
    );
  }
}

export default AddTopics;
